.questionnaire {
	background-color: #56AEDD;
	color: #FFF;
	padding-top: 50px;
	padding-bottom: 50px;
	h3 {
		font-size: 2.25rem;
		text-align: center;
		margin-bottom: 40px;
		@media screen and (max-width:767px) {
			width: 90%;
			margin: 0 auto 40px;
			img {
				width: 100%;
			}
		}
			.bigger {
				font-size: 40px;
			}
			.slash {
				margin: 0 15px;
				font-weight: 300;
				font-size: 45px;
			}
		@media screen and (max-width:767px) {
			font-size: 1.6rem;
		}
	}
	.text-center {
		text-align: center;
		margin-top: 70px;
		.text-right {
			text-align: right;
		}
	}
	.btn {
        background-color: #6D7E8C;
        border-radius: 5px;
        cursor: pointer;
        height: 45px;
        line-height: 45px;
        margin: 50px auto 0;
        text-align: center;
        width: 390px;
		@media screen and (max-width:425px) {
			width: 90%;
			a {
				font-size: 14px;
			}
        }
        a {
            text-decoration: none;
            color: #FFF;
        }
    }
	.customer-answer {
		display: flex;
		width: 1000px;
		margin: 0 auto;
		justify-content: space-between;
		.voice {
			background-color: #FFF;
			color: #56AEDD;
			font-size: 1.5rem;
			border-radius: 5px;
			font-weight: bold;
			position: relative;
			width: 300px;
			box-sizing: border-box;
			padding: 45px 38px;
			&:after {
                position: absolute;
                content: "";
                left: 43%;
                bottom: -33px;
                border-top: 0 solid #ffffff;
                border-left: 0 solid #f5d97b6e;
                border-bottom: 0 solid #D94948;
                border-right: 0 solid #8db43400;
                border-top-width: 35px;
                border-left-width: 0px;
                border-right-width: 35px;
                border-bottom-width: 0px;
            }
		}
		@media screen and (max-width: 767px) {
            width: 95%;
            margin: 0 auto;
			display: block;
			.voice {
				width: 80%;
				padding: 10px;
				margin: 0 auto 40px;
				p {
					font-size: 18px;
					line-height: 1.4;
				}
			}
        }
	}
}


.comparing {
	color: #FFF;
	padding-top: 90px;
	position: relative;
	padding-bottom: 70px;
	h3 {
		font-size: 36px;
		line-height: 54px;
		text-align: center;
	}
	&::before {
		content: '';
		position: absolute;
		display: block;
		z-index: 1;
		border-style: solid;
		border-color: #01588E transparent;
		border-width: 20px 20px 0 20px;
		bottom: -20px;
		left: 50%;
		margin-left: -10px;
	}
}

.top-graph {
	background-color: #F5F5F5;
	padding-top: 90px;
	padding-bottom: 90px;
	.inner {
		margin: 0 auto;
		width: 1000px;
		img {
			width: 100%;
		}
		@media screen and (max-width:767px) {
			width: 100%;
		}
	}
	.btn {
        background-color: #6D7E8C;
        border-radius: 5px;
        cursor: pointer;
        height: 45px;
        line-height: 45px;
        margin: 50px auto 0;
        text-align: center;
        width: 430px;
		@media screen and (max-width:425px) {
			width: 90%;
			a {
				font-size: 14px;
			}
        }
        a {
            text-decoration: none;
            color: #FFF;
        }
    }
}