.suggestion {
    .suggest-top {
        text-align: center;
        padding: 50px;
        color: #FFF;
        position: relative;
        background-position-y: 70%;
        @media screen and (max-width:767px) {
            padding: 10px;
            h3 {
                font-size: 28px;
            }
        }
        h4.under-line {
            border-bottom: 3px solid #FADD62;
            width: fit-content;
            margin: 0 auto;
            font-size: 24px;
            line-height: 42px;
            padding-bottom: 6px;
        }
        h3 {
            margin-top: 30px;
            font-size: 36px;
            line-height: 54px;
        }
        ::before {
            content: '';
            position: absolute;
            display: block;
            z-index: 1;
            border-style: solid;
            border-color: #035aa6 transparent;
            border-width: 20px 10px 0 10px;
            bottom: -20px;
            left: 50%;
            margin-left: -10px;
        }
    }
    .suggest-middle {
        padding-top: 65px;
        background-color: #F5F5F5;
        padding-bottom: 65px;
        .check-list {
            display: flex;
            margin: 0 auto;
            width: fit-content;
            @media screen and (max-width:767px) {
                width: 90%;
                .check-sin {
                    margin-right: 10px;
                }
                .right {
                    width: 100%;
                }
            }
            .check-sin {
                margin-right: 23px;
                font-size: 20px;
            }
        }
        p {
            margin-right: auto;
            margin-left: auto;
            width: 645px;
            @media screen and (max-width:767px) {
                width: 90%;
                line-height: 1.3;
            }
        }
        .check {
            font-size: 20px;
            margin-bottom: 30px;
            padding-bottom: 10px;
            border-bottom: 1px solid #707070;
            @media screen and (max-width:767px) {
                font-size: 3.5vw;
            }
        }
    }
}
